const load = async ({ locals, url }) => {
  if (!locals.session) {
    return null;
  }
  const user = await prisma.user.findUnique({
    where: {
      id: locals.session?.user?.id
    },
    select: {
      id: true,
      name: true,
      email: true,
      image: true
    }
  });
  return { user };
};
export {
  load
};
